.move-button {
    background-color: #ff76ff;
    margin-top: 2em;
}

.move-text {
    margin-top: 2em;
}

input.move-input::-webkit-outer-spin-button,
input.move-input::-webkit-inner-spin-button {
    -webkit-appearance: none;
    margin: 0;
}

input.move-input[type=number] {
    -moz-appearance:textfield;
}

input.move-input {
    width: 10em;
    margin-left: 0.4em;
    margin-right: 0.2em;
}

.move-warning {
    color: #660000;
}
